import React, { Component } from 'react'

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">Highlight Projects</span>
                <h2 className="colorlib-heading animate-box">Highlights</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-1">
                        <i className="icon-warning" />
                      </div>
                      <div className="timeline-label">
                        <h2>Witness (Police Brutality App) <span> July 2020</span></h2>
                        <p>•  Created during IBM's Blue Movement Bootcamp. Used Kivy (Python Framework) and Firebase APIs to develop a cross-platform app that worked on increasing the amount of police brutality indictments through providing solid audio evidence that saves in multiple storages including the user's phone, the user's contacts, and our cloud storage.</p> <p>• We also worked on creating a review and report system in order to give users a sense of community and support. The report system allows users to create reports on interactions with police which is sent to our database so we can verify and report to the local police department. Once verified, that post can be put in our search engine function. This search engine creates a review platform that allows users to find reviews of officers that they have had issues with in the past as well as helps highlight the police doing their job well to create a platform that brings together all of our users to overcome injustice as a community.</p>
 <p> •  Was selected as a team lead as a rising sophomore over graduate students and seniors. Worked on recording and sending audio using native Android and IOS functions. Had to make last minute adjustments due to deployment issues and began working on using native Windows and Mac OS functions to record audio in order to have a presentable demo.</p><p>• Resolved issue of audio files being too large to send by teaching myself how to use Firebase Cloud Storage to send links instead.</p> <p>• Cooperated with the UI team and database team to help resolve any problems that arose in their code as well as combining front-end and back-end scripts. Available on my <a href="https://github.com/CodingWithChrisB/IBM-Project"> Github!</a></p>
 <p> To see in action, check out our commercial! <a href="https://youtu.be/T4BKx4MgYcs">Click Here!</a></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-location" />
                      </div>
                      <div className="timeline-label">
                        <h2>Guardian Angel (Anti Sex Trafficking App) <span> May 2020</span></h2>
                        <p>• GuardianAngel is a cross Android and IOS platform app that protects users from sex trafficking, kidnapping, and other crimes that could potentially cause the user to go missing.</p> <p>• Given the amount of people who go missing each year at the hands of sex trafficking, I felt as though this was an extremely important topic to cover. I don't want my future family or the next generation of children to have to live in fear that they could become a victim of human trafficking. </p>
                        <p>• This is done by sending the an extremely precise user's location that includes latitude, longitude, altitude to tell which floor they're on or if they're below sea level and even the speed at which they are moving in the form of an sms to the user's contacts and law enforcement every 3 minutes. In future updates, we will be adding audio, camera footage, and more! Available on my <a href="https://github.com/7daylenrobertson/GuardianAngel"> Github!</a></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-1">
                        <i className="icon-device-desktop" />
                      </div>
                      <div className="timeline-label">
                        <h2>Open World VR Programming <span> April 2020</span></h2>
                        <p>• As programmers, we tend to feel like we don't have much of a workspace when we are sitting at a desk. Have you ever wanted to walk around and code in your open environment? Now you can! Through this, you can have more thinking space as well as code comfortably in the car, in bed, even outside! All of this while still feeling like you have all the space in the world In multiple environments such as a beach or a the outside landscape of a google office! This is to help stimulate creativity and accesibitlity in the art of coding</p> 
                        <p>• Taught myself C# and Unity Game Development in the span of 24hrs in order to create a 360 virtual reality programming simulator where users can use controllers or other devices in their home to program and move freely in a relaxing, customizable environment rather than be confined to a desk.</p>
                        <p>• Software was created during Google’s Hack DSC event and despite still being created at the time, received an honorable mention. Available on my <a href="https://github.com/7daylenrobertson/Open-VR-Programming"> Github!</a></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
